html,
body,
#root {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  position: relative;
  background: url("https://78.media.tumblr.com/d808fef0da889f09742d3a074f356bc5/tumblr_paaphnKhwY1w0o6cgo1_400.png");
}

.app-parent-container {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.area-chart-modal-container {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.data-loading-h1 {
  position: absolute;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation: load-pulse;
          animation: load-pulse;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  z-index: 2;
}

@-webkit-keyframes load-pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
  }
}

@keyframes load-pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
  }
}



